
.star-rating__checkbox {
    position: absolute;
    overflow: hidden;
    clip: rect(0 0 0 0);
    height: 1px;
    width: 1px;
    margin: -1px;
    padding: 0;
    border: 0;
}

.star-rating__star {
    display: inline-block;
    padding: 0px;
    vertical-align: middle;
    line-height: 1;
    font-size: 2.2em;
    font-family: Sans-Serif;
    color: #ABABAB;
    -webkit-transition: color;
    transition: color;
}
.star-rating__star:hover {
    cursor: pointer;
}
.star-rating__star.is-selected {
    color: #3498DB;
}
.star-rating__star.is-disabled:hover {
    cursor: default;
}




